.Deck__deckCard {
    background-color: #222 !important;
    position: relative;
    width: 290px;
    margin: 15px;
    padding: 7px;
}

.Deck__deckHead {
    width: 100%;
    padding-top: 15px;
    background-color: #fdfcf3;
}

.Deck_link {
    text-decoration: none;
}

.Deck__title {
    font-family: "Beleren Bold";
    font-size: 23px;
    display: block;
    color: #222;
    cursor: pointer;
}

.Deck__title:hover {
    color: #5297E0;
    -webkit-transition: all 0.45s ease-in-out;
    -o-transition: all 0.45s ease-in-out;
    transition: all 0.45s ease-in-out;
}

.Deck__avatar {
    position: relative;
    height: 100px;
}

.Deck__cardHeadlinerTop {
    background-color: #fdfcf3;
    height: 50px;
}

.Deck__cardHeadlinerWrapper {
    padding: 5px;
    background-color: #f9f9f9;
    border-radius: 50%;
    width: 80px;
    position: absolute;
    bottom: 5px;
    right: 20px;
    -webkit-filter: grayscale(.6);
            filter: grayscale(.6);
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
    cursor: pointer;
}

.Deck__cardHeadlinerWrapper:hover {
    -webkit-filter: grayscale(.1);
            filter: grayscale(.1);
}

.Deck__cardHeadliner {
    height: 80px;
    width: 80px;
    border-radius: 50%;
    display: block;
    background-size: 140px;
    background-position: -34px -22px;
}

.Deck__cardHeadlinerBottom {
    height: 50px;
    background-color: #222;
}

.Deck__cardManaPool {
    margin: 0 auto;
}

.Deck__manaWrapper {
    margin: 5px;
}

.Deck__composition {
    display: none;
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
}

.Deck__compositionWrapper {

}

.Deck__cardMana {
    padding: 0;
    margin: 0;
}

.Deck__cardManaNumber {
    color: #e2e2e2;
    font: 14px Helvetica;
}

.Deck__manaBar {
    top: 50px;
    right: 0;
    padding: 10px 0;
    white-space: nowrap;
    vertical-align: top;
    background-color: #222;
}

.Deck__mana {
    display: inline-block;
    margin: 1px;
    height: 25px;
}

.Deck__mana .ms {
    color: #fff;
    font-size: 13px;
    line-height: 24px;
}

.type {
    width: 15px;
    height: 15px;
    margin: 3px;
    padding: 5px;
    border-radius: 50%;
    background-color: #e2e2e2;
}

.mana {
    margin: 3px;
    padding: 7px;
    border-radius: 50%;
    background-color: #222;
    color: #f9f9f9;
    font-size: 20px;
    height: 20px;
    width: 20px;
    -webkit-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
}

.mana:hover {
    background-color: #f9f9f9;
    color: #222;
}

.mana-r {
    background-color: #D32F2F;
}

.mana-g {
    background-color: #388E3C;
}

.mana-b {
    background-color: #616161;
}

.mana-u {
    background-color: #1976D2;
}

.mana-w {
    background-color: #FFEA00;
}



